import React from "react";
import './Endpoint.css'

class Endpoint extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            function_list: [],
            current_path: "",
            data: {},
            path: "",
            rs_type: "",
            create: true
        };
    }

    onclick(path) {
        this.setState({current_path: path})
    }


    render() {
        return (
            <div>
                <div className={"ep_header_bar"}/>
                <div className="ep_main_frame">
                    <div className={"left_bar"}>
                        {
                            this.state.function_list.map((i, j) => {
                                    return <div className="item" onClick={() => this.onclick(i)}>{i}</div>
                                }
                            )
                        }
                    </div>
                    <div>
                        <div className={"ep_right_content"}>
                            <div className={"ep_edit_panel"}>
                                <form name="my_form"
                                      className={"ep_form"}
                                      action={this.backend_path + "/endpoint/create"}
                                      method={"post"}
                                      target={"ep_save_iframe"}>
                                    {
                                        this.state.create &&
                                        <div className={"endpoint"}> 自定义路径:
                                            <input type="text" name={"path"} onChange={p => {
                                                this.setState({path: p.target.value})
                                            }}
                                            />
                                            <a href={this.api_gateway_path  + this.state.path}>{this.api_gateway_path  + this.state.path}</a>
                                        </div>
                                    }


                                    <div> 请求方法 :</div>
                                    <div>
                                        <input name="methods" value={"GET"} type="checkbox"/> GET
                                        <input name="methods" value={"POST"} type="checkbox"/> POST
                                        <input name="methods" value={"PATCH"} type="checkbox"/> PATCH
                                        <input name="methods" value={"DELETE"} type="checkbox"/> DELETE
                                    </div>
                                    <div>资源类型：</div>
                                    <div>
                                        <input name="rs_type" value="code"
                                               onClick={() => this.setState({rs_type: "code"})}
                                               type="radio"/>function
                                        <input name="rs_type" value="img"
                                               onClick={() => this.setState({rs_type: "img"})}
                                               type="radio"/>image
                                        <input name="rs_type" value="application/json"
                                               onClick={() => this.setState({rs_type: "json"})}
                                               type="radio"/>json
                                        <input name="rs_type" value="text/plain"
                                               onClick={() => this.setState({rs_type: "text"})}
                                               type="radio"/>text
                                        <input name={"rs_type"} value={null}
                                               onClick={() => this.setState({rs_type: "other"})}
                                               type="radio"/>other
                                        static resource
                                    </div>
                                    <div>
                                        {
                                            this.state.rs_type === "code" &&
                                            <div>
                                                function name
                                                <input name="function_name" type="text"/>
                                            </div>
                                        }
                                        {
                                            this.state.rs_type === "img" &&
                                            <div>
                                                <img/>
                                                select file<input name={"file"} type="file"/>
                                            </div>
                                        }
                                        {
                                            this.state.rs_type === "json" &&
                                            <div><textarea name="data">json</textarea></div>
                                        }
                                        {
                                            this.state.rs_type === "text" &&
                                            <div>
                                                <textarea name="data">text</textarea>
                                            </div>
                                        }
                                        {
                                            this.state.rs_type === "other" &&
                                            <div>
                                                mime<input name={"rs_type"} type="text"/>
                                                <input name="file" type="file"/>
                                            </div>
                                        }
                                    </div>
                                    <div>鉴权：</div>
                                    <div>
                                        <input type="radio" name="auth"/>cookie
                                        <input type="radio" name="auth"/>header
                                    </div>

                                    <div>
                                        <input type="checkbox" name="cache"/>缓存
                                    </div>

                                    <div>
                                        <input type="submit" onClick={this.onSave} value="创建"/>
                                    </div>
                                </form>
                            </div>
                            <iframe name={"ep_save_iframe"} className={"ep_save_iframe"}/>
                        </div>
                    </div>

                </div>
            </div>

        )
    }

    componentDidMount() {
        fetch(this.backend_path + '/endpoint/get_all')
            .then(res => res.json())
            .then(json => {
                let temp = []
                for (let i in json) {
                    temp.push(i)
                }
                this.setState({function_list: temp, data: json})
                console.log("/endpoint/get_all")
                console.log(json)
            })
    }
}

export default Endpoint