import React from "react";
import './Dashboard.css'

class DashBoard extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            info: {cpu: 0, mem: 0, running: 0}
        };
    }


    render() {
        return (
            <div>
                <div>
                    <div className={"system_info"}>
                        <div>后端地址：{this.backend_path}</div>
                        <div>API网管地址：{this.api_gateway_path}</div>
                        <div>worker数量：3</div>
                    </div>
                </div>


                <div className={"summary"}>
                    <div>CPU:{this.state.info.cpu}%</div>
                    <div>MEM:{this.state.info.mem}%</div>
                    <div>Containers Up:
                        {this.state.info.running}
                    </div>
                </div>
                <div>

                </div>
            </div>

        )
    }

    get_info() {
        fetch(this.backend_path + '/dashboard/summary')
            .then(res => res.json())
            .then(res => {
                this.setState({info: res})
                console.log("info", res)
            })
    }

    componentDidMount() {
        // setInterval(() => {
        //     this.get_info()
        // }, 1000)
        this.get_info()
    }
}

export default DashBoard